    .counter__item::before {
        display: none;
    }

    .counter__content {
        padding: 0;
    }

    .counter__item.second__item {
        margin-top: 0;
    }

    .counter__item.four__item {
        margin-top: 0;
    }

    // .counter__item__text {
    //     -webkit-transform: rotate(0);
    //     transform: rotate(0);
    // }

    .counter {
        height: auto;
        padding-top: 100px;
        padding-bottom: 70px;
    }

    .team__item.team__item--second {
        margin-top: 0;
    }

    .team__item.team__item--third {
        margin-top: 0;
    }

    .team__item.team__item--four {
        margin-top: 0;
    }

    .team__btn {
        position: relative;
        padding-top: 50px;
    }

    .about__pic {
        margin-bottom: 50px;
    }

    .about__text {
        padding-left: 0;
    }

    .hero__slider.owl-carousel .owl-dots {
        max-width: auto;
        // padding: 0 15px;
    }

    .header {

        .container {
            position: relative;
        }
    }

    .header__nav__option {
        display: none;
    }

    // .hero__text h2 {
    //     font-size: 40px;
    //     line-height: 40px;
    // }

    .grid-sizer {
        width: calc(50% - 10px);
    }

    .work__item.wide__item {
        width: calc(50% - 10px);
    }

    .work__item {
        width: calc(50% - 10px);
    }

    .work__item.large__item {
        width: calc(50% - 10px);
    }

    .footer__top__social {
        text-align: left;
        padding-top: 30px;
    }

    .portfolio__filter li {
        margin-bottom: 10px;
    }

    .input__list {
        margin-right: 0;
    }

    .input__list input {
        width: 100%;
        margin-right: 0;
    }

    .contact__map {
        margin-bottom: 40px;
    }

    .team__item {
        margin-bottom: 20px;
    }

    .team {

        .p-0 {
            padding: 0 15px !important;
        }
    }

    .about__pic__item {
        margin-right: 0;
    }

    .blog__details__quote {
        padding: 30px 45px 35px 35px;
    }
}

/* Small Device = 320px */
@media only screen and (max-width: 479px) {
    // .hero__text h2 {
    //     font-size: 30px;
    //     line-height: 40px;
    // }

    .grid-sizer {
        width: 100%;
    }

    .work__item.wide__item {
        width: 100%;
    }

    .work__item {
        width: 100%;
        margin-right: 0;
    }

    .work__item.large__item {
        width: 100%;
    }

    .work__gallery {
        margin-right: 0;
    }

    .pagination__option .arrow__pagination.left__arrow {
        margin-right: 15px;
    }

    .pagination__option .arrow__pagination.right__arrow {
        margin-left: 10px;
    }

    .blog__details__quote {
        padding: 30px 40px 35px 35px;
    }
}